<template>
  <div class="home row l-r">
    <div class="home_left_box">
      <!-- 搜索 -->
      <div class="search_box">
        <Input search enter-button="搜索" placeholder="请输入关键字词" class="input_text" />
      </div>
      <!-- 分类 -->
      <div class="classify_box box">
        <h2>分类</h2>
        <ul>
          <li>文章（13）</li>
          <li>相册（8）</li>
        </ul>
      </div>
      <!-- 推荐 -->
      <div class="recommend box">
        <h2>我的推荐</h2>
        <ul>
          <li>
            <span>个人博客，我为什么要用帝国cms？</span>
          </li>
          <li>
            <span>一个三十而立的男程序员真实讲述：代码搅乱我的生活</span>
          </li>
          <li>
            <span>古典个人博客模板《青砖屋檐》</span>
          </li>
          <li>
            <span>个人博客，属于我的小世界！</span>
          </li>
          <li>
            <span>抄袭门过后——丢掉心结，重拾自己</span>
          </li>
          <li>
            <span>个人博客从简不繁</span>
          </li>
          <li>
            <span>个人博客模板《早安》</span>
          </li>
          <li>
            <span>简单手工纸玫瑰</span>
          </li>
        </ul>
      </div>
      <!-- 标签云 -->
      <div class="tag_cloud box">
        <h2>标签云</h2>
        <ul>
          <li>
            <button>个人博客</button>
            <button>旅游</button>
          </li>
        </ul>
      </div>
      <!-- 关注我 -->
      <div class="follow_me box">
        <h2>关注我</h2>
        <ul>
          <img src="../../.././assets/file/home/follow_me.jpg" alt />
        </ul>
      </div>
    </div>
    <div class="home_right_box">
      <!--推荐模板标签-->
      <li class="news_title">一个三十而立的男程序员真实讲述：代码搅乱我的生活</li>
      <div class="bloginfo">
        <ul>
          <li class="author"></li>
          <li class="lmname">文章</li>
          <li class="timer">2018-07-24</li>
          <li class="count">
            <span>3649</span>人已阅读
          </li>
        </ul>
      </div>
      
      <div class="point_box">
        <Button class="zan">很赞哦<span>(42)</span></Button>
      </div>
    </div>
  </div>
</template>

<script>
import scrollReveal from "scrollreveal";
export default {
  data() {
    return {
      scrollReveal: scrollReveal()
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9
    });
  }
};
</script>
<style lang="scss" src="@/assets/scss/photo_details.scss" scoped>
</style>


